"use client";

import { useChat } from '@ai-sdk/react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Bot, User, Send, Loader2 } from 'lucide-react';
import { useEffect, useRef, useState } from 'react'; // ‚úÖ ÂºïÂÖ• useState

export default function AICompanionPage() {
  // ‚úÖ 1. Êàë‰ª¨Ëá™Â∑±ÁÆ°ÁêÜËæìÂÖ•Ê°ÜÁä∂ÊÄÅÔºå‰∏ç‰æùËµñ SDKÔºåËøôÊ†∑ÁªùÂØπ‰∏ç‰ºöÊä•Èîô
  const [localInput, setLocalInput] = useState('');

  // ‚úÖ 2. Âè™‰ªé SDK ÈáåÂèñÂá∫ append (ÂèëÈÄÅÊ∂àÊÅØ) Âíå messages (Ê∂àÊÅØÂàóË°®)
  const { messages, append, isLoading } = useChat({
    api: '/api/chat',
    onError: (err) => {
      console.error("AI Âá∫Èîô‰∫Ü:", err);
    }
  });

  // Ëá™Âä®ÊªöÂä®Âà∞Â∫ïÈÉ®
  const scrollRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTo({ top: scrollRef.current.scrollHeight, behavior: 'smooth' });
    }
  }, [messages]);

  // ‚úÖ 3. Ëá™ÂÆö‰πâÂèëÈÄÅÂáΩÊï∞
  const handleSend = async (e?: React.FormEvent) => {
    e?.preventDefault(); // ÈòªÊ≠¢Ë°®ÂçïÈªòËÆ§Êèê‰∫§
    
    if (!localInput.trim() || isLoading) return;

    const content = localInput;
    setLocalInput(''); // Á´ãÂç≥Ê∏ÖÁ©∫ËæìÂÖ•Ê°Ü
    
    // Ë∞ÉÁî® SDK ÁöÑ append ÊñπÊ≥ïÂèëÈÄÅÊ∂àÊÅØ
    await append({
      role: 'user',
      content: content,
    });
  };

  return (
    <div className="flex flex-col h-[calc(100vh-100px)] max-w-3xl mx-auto p-4">
      {/* È°∂ÈÉ®ÊèêÁ§∫ */}
      <div className="bg-blue-50 text-blue-700 p-3 rounded-lg text-sm mb-4 text-center shadow-sm">
        ü§ñ <b>AI Èô™‰º¥Âä©ÊâãÂ∞èÊöñ</b> (24Â∞èÊó∂Âú®Á∫ø)<br/>
        <span className="text-xs opacity-80">ÊàëÊòØ‰∏Ä‰∏™‰∫∫Â∑•Êô∫ËÉΩÔºåÂõûÂ§ç‰ªÖ‰æõÂèÇËÄÉ„ÄÇÂ¶ÇÈÅáÁ¥ßÊÄ•ÊÉÖÂÜµÔºåËØ∑Âä°ÂøÖÂØªÊ±Ç‰∫∫Â∑•Â∏ÆÂä©„ÄÇ</span>
      </div>

      {/* Ê∂àÊÅØÂàóË°®Âå∫Âüü */}
      <ScrollArea ref={scrollRef} className="flex-1 pr-4 border rounded-xl p-4 bg-white shadow-sm mb-4">
        {messages.length === 0 && (
            <div className="h-full flex flex-col items-center justify-center text-slate-400">
                <div className="w-16 h-16 bg-slate-100 rounded-full flex items-center justify-center mb-4">
                  <Bot className="w-8 h-8 text-[#5D9C59]" />
                </div>
                <p>‰Ω†Â•ΩÂëÄÔºÅÊàëÊòØÂ∞èÊöñ„ÄÇ</p>
                <p className="text-sm mt-2">ÊúÄËøëÊúâ‰ªÄ‰πàÂøÉ‰∫ãÂêóÔºüÈöèÊó∂ÂèØ‰ª•ÂíåÊàëËØ¥ËØ¥„ÄÇ</p>
            </div>
        )}
        
        {messages.map((m, index) => (
          <div key={m.id || index} className={`flex gap-3 mb-6 ${m.role === 'user' ? 'justify-end' : 'justify-start'}`}>
            {/* Êú∫Âô®‰∫∫Â§¥ÂÉè */}
            {m.role !== 'user' && (
              <Avatar className="w-8 h-8 border">
                <AvatarImage src="/ai-avatar.png" />
                <AvatarFallback className="bg-[#E8F5E9]"><Bot className="w-4 h-4 text-[#5D9C59]" /></AvatarFallback>
              </Avatar>
            )}
            
            {/* Ê∂àÊÅØÊ∞îÊ≥° */}
            <div className={`px-4 py-3 rounded-2xl max-w-[85%] text-sm leading-relaxed shadow-sm ${
              m.role === 'user' 
                ? 'bg-[#5D9C59] text-white rounded-br-none' 
                : 'bg-white border border-slate-100 text-slate-700 rounded-bl-none'
            }`}>
              {m.content}
            </div>

            {/* Áî®Êà∑Â§¥ÂÉè */}
            {m.role === 'user' && (
              <Avatar className="w-8 h-8 border">
                <AvatarFallback className="bg-slate-100"><User className="w-4 h-4 text-slate-600" /></AvatarFallback>
              </Avatar>
            )}
          </div>
        ))}
        
        {/* Âä†ËΩΩÁä∂ÊÄÅ */}
        {isLoading && (
          <div className="flex gap-3 mb-4 justify-start">
             <Avatar className="w-8 h-8 border">
                <AvatarFallback className="bg-[#E8F5E9]"><Bot className="w-4 h-4 text-[#5D9C59]" /></AvatarFallback>
              </Avatar>
              <div className="flex items-center text-xs text-slate-400">
                <Loader2 className="w-3 h-3 animate-spin mr-1" /> Â∞èÊöñÊ≠£Âú®ÊÄùËÄÉ...
              </div>
          </div>
        )}
      </ScrollArea>

      {/* ËæìÂÖ•Ê°ÜÂå∫Âüü */}
      <form onSubmit={handleSend} className="flex gap-2 items-center bg-white p-2 rounded-xl border shadow-sm">
        <Input 
          // ‚úÖ 4. ÁªëÂÆöÊàë‰ª¨Ëá™Â∑±ÁöÑ localInputÔºåÁªùÂØπÁ®≥
          value={localInput} 
          onChange={(e) => setLocalInput(e.target.value)}
          placeholder="Âú®ËøôÈáåËæìÂÖ•‰Ω†ÊÉ≥ËØ¥ÁöÑËØù..." 
          className="flex-1 border-0 focus-visible:ring-0 shadow-none bg-transparent"
        />
        <Button 
          type="submit" 
          disabled={isLoading || !localInput.trim()} 
          className="bg-[#5D9C59] hover:bg-[#4a8546] text-white rounded-lg w-10 h-10 p-0 flex items-center justify-center transition-all"
        >
          {isLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Send className="w-4 h-4" />}
        </Button>
      </form>
    </div>
  );
}